body {
  background: black;
}

.App {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  margin-top: 30px;
}

.user-card-wrapper {
  display: flex;
  margin: auto 20px;
}

.user-card {
  height: 250px;
  width: 200px;
  border-radius: 20px;
  background: white;
  cursor: pointer;
}

.user-card:hover {
  transform: translateY(-10px);
  transition: box-shadow 0.1s linear, transform 0.15s ease-in-out;
}

.user-card-avatar {
  text-align: center;
}

.user-avatar {
  border-radius: 50%;
  margin-top: 25px;
}

.user-card-email {
  text-align: center;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}

.user-card-name {
  text-align: center;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}
